import { useRouter } from "next/router";
import { useState, useEffect, useRef } from "react";
import { getPostCategories } from "@/lib/api";
import style from "./create.module.scss";
import classnames from "classnames/bind";
import CustomHead from "@/components/CustomHead";
import Link from "next/link";

const cx = classnames.bind(style);

type Props = {
  categoryList: string[];
};

export default function CreatePost({ categoryList }: Props) {
  const router = useRouter();
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const editorRef = useRef<HTMLDivElement>(null);
  const [category, setCategory] = useState(categoryList[0] || "");
  const [description, setDescription] = useState("");
  const [coverImage, setCoverImage] = useState("");
  const [tags, setTags] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState("");
  const [isUploading, setIsUploading] = useState(false);

  // ÏóêÎîîÌÑ∞ ÎÜíÏù¥Î•º ÎèôÏ†ÅÏúºÎ°ú Ï°∞Ï†ïÌïòÎäî Ìö®Í≥º
  useEffect(() => {
    const handleResize = () => {
      const headerHeight = document.querySelector(`.${cx("editor_header")}`)?.clientHeight || 0;
      const footerHeight = document.querySelector(`.${cx("editor_footer")}`)?.clientHeight || 0;
      const windowHeight = window.innerHeight;

      const editor = document.querySelector(`.${cx("editor_body")}`) as HTMLElement;
      if (editor) {
        const availableHeight = windowHeight - headerHeight - footerHeight - 120;
        editor.style.height = `${availableHeight}px`;
      }
    };

    handleResize();
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // ÏóêÎîîÌÑ∞ Ï¥àÍ∏∞Ìôî
  useEffect(() => {
    if (editorRef.current) {
      // ÏóêÎîîÌÑ∞ Ìè¨Ïª§Ïä§Ïãú Í∏∞Î≥∏ p ÌÉúÍ∑∏ Ï∂îÍ∞Ä
      editorRef.current.addEventListener("focus", () => {
        if (editorRef.current && editorRef.current.innerHTML === "") {
          editorRef.current.innerHTML = "<p><br></p>";
        }
      });

      // ÏΩòÌÖêÏ∏† Î≥ÄÍ≤Ω Í∞êÏßÄ
      editorRef.current.addEventListener("input", () => {
        if (editorRef.current) {
          setContent(editorRef.current.innerHTML);
        }
      });
    }
  }, []);

  // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ìï®Ïàò
  const uploadImage = async (file: File): Promise<string> => {
    setIsUploading(true);
    try {
      // FormData ÏÉùÏÑ±
      const formData = new FormData();
      formData.append("image", file);

      // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú API Ìò∏Ï∂ú (Ïã§Ï†ú APIÎ°ú Î≥ÄÍ≤Ω ÌïÑÏöî)
      // Ïã§Ï†ú Íµ¨ÌòÑÏóêÏÑúÎäî Ïù¥ Î∂ÄÎ∂ÑÏùÑ ÏÑúÎ≤ÑÏóê ÎßûÍ≤å ÏàòÏ†ïÌïòÏÑ∏Ïöî
      const response = await fetch("http://localhost:3001/api/upload-image", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®");
      }

      const result = await response.json();
      return result.imageUrl; // ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄ URL Î∞òÌôò

      // Í∞úÎ∞úÏö© ÌÖåÏä§Ìä∏ ÏΩîÎìú (Ïã§Ï†ú Íµ¨ÌòÑ Ïãú ÏÇ≠Ï†ú)
      // return URL.createObjectURL(file);
    } catch (err) {
      console.error("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïò§Î•ò:", err);
      throw err;
    } finally {
      setIsUploading(false);
    }
  };

  // Ïù¥ÎØ∏ÏßÄ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ï≤òÎ¶¨
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (editorRef.current) {
      editorRef.current.classList.add(cx("drag-over"));
    }
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (editorRef.current) {
      editorRef.current.classList.remove(cx("drag-over"));
    }
  };

  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();

    if (editorRef.current) {
      editorRef.current.classList.remove(cx("drag-over"));

      const files = Array.from(e.dataTransfer.files);
      const imageFiles = files.filter((file) => file.type.startsWith("image/"));

      if (imageFiles.length === 0) return;

      for (const imageFile of imageFiles) {
        try {
          const imageUrl = await uploadImage(imageFile);
          insertImageToEditor(imageUrl);
        } catch (err) {
          setError("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        }
      }
    }
  };

  // ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóêÏÑú Ïù¥ÎØ∏ÏßÄ Î∂ôÏó¨ÎÑ£Í∏∞ Ï≤òÎ¶¨
  const handlePaste = async (e: React.ClipboardEvent) => {
    const items = e.clipboardData.items;

    for (let i = 0; i < items.length; i++) {
      if (items[i].type.indexOf("image") !== -1) {
        e.preventDefault();

        const file = items[i].getAsFile();
        if (!file) continue;

        try {
          const imageUrl = await uploadImage(file);
          insertImageToEditor(imageUrl);
        } catch (err) {
          setError("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        }

        break;
      }
    }
  };

  // ÏóêÎîîÌÑ∞Ïóê Ïù¥ÎØ∏ÏßÄ ÏÇΩÏûÖ
  const insertImageToEditor = (imageUrl: string) => {
    if (!editorRef.current) return;

    const selection = window.getSelection();
    if (!selection || !selection.rangeCount) return;

    const range = selection.getRangeAt(0);
    const img = document.createElement("img");
    img.src = imageUrl;
    img.className = cx("editor-image");

    range.insertNode(img);
    range.setStartAfter(img);
    range.collapse(true);

    selection.removeAllRanges();
    selection.addRange(range);

    // ÏΩòÌÖêÏ∏† ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    setContent(editorRef.current.innerHTML);
  };

  // Ìèº Ï†úÏ∂ú Ï≤òÎ¶¨
  const handleSubmit = async () => {
    // Form validation
    if (!title.trim()) {
      setError("Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    if (!content || content === "<p><br></p>") {
      setError("ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    try {
      setIsSubmitting(true);
      setError("");

      // ÌïúÍ∏ÄÏùÑ Ìè¨Ìï®Ìïú Ïä¨Îü¨Í∑∏ ÏÉùÏÑ±
      const slug = title
        .toLowerCase()
        .replace(/[^a-zA-Z0-9Í∞Ä-Ìû£\s]/gi, "") // ÏòÅÎ¨∏, Ïà´Ïûê, ÌïúÍ∏Ä, Í≥µÎ∞±Îßå ÌóàÏö©
        .replace(/\s+/g, "-");

      const postData = {
        title,
        content,
        category,
        description,
        coverImage,
        slug,
        tags: tags.split(" ").filter((tag) => tag.startsWith("#")),
        date: new Date().toISOString(),
      };

      console.log(postData);

      const response = await fetch("http://localhost:3001/api/create-post", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(postData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Ìè¨Ïä§Ìä∏ ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }

      const result = await response.json();

      alert("Ìè¨Ïä§Ìä∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÉùÏÑ±ÎêòÏóàÏäµÎãàÎã§.");
      router.push("/admin");
    } catch (err) {
      console.error("Error creating post:", err);
      setError(err instanceof Error ? err.message : "Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsSubmitting(false);
    }
  };

  // ÏóêÎîîÌÑ∞ ÎèÑÍµ¨ Î≤ÑÌäº Ìï∏Îì§Îü¨
  const handleBold = () => {
    document.execCommand("bold", false);
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleItalic = () => {
    document.execCommand("italic", false);
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleUnderline = () => {
    document.execCommand("underline", false);
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleHeading = () => {
    document.execCommand("formatBlock", false, "<h2>");
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleParagraph = () => {
    document.execCommand("formatBlock", false, "<p>");
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleStrikethrough = () => {
    document.execCommand("strikethrough", false);
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  const handleAlign = (align: string) => {
    document.execCommand(`justify${align}`, false);
    if (editorRef.current) setContent(editorRef.current.innerHTML);
  };

  return (
    <>
      <CustomHead type="home" />
      <div className={cx("container")}>
        {/* ÏóêÎîîÌÑ∞ Ïä§ÌÉÄÏùº Ìó§Îçî */}
        <div className={cx("editor_header_bar")}>
          <div className={cx("editor_header_left")}>
            <Link href="/admin" className={cx("logo")}>
              SeedDividend
            </Link>
          </div>

          <div className={cx("editor_toolbar")}>
            <button type="button" className={cx("toolbar_btn")} onClick={() => {}} title="Î≥∏Î¨∏2">
              <span>Î≥∏Î¨∏2</span>
              <span className={cx("dropdown_arrow")}>‚ñæ</span>
            </button>

            <span className={cx("toolbar_divider")}></span>

            <button type="button" className={cx("toolbar_btn")} onClick={handleBold} title="ÍµµÍ≤å">
              <span className={cx("toolbar_icon", "bold")}>B</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} onClick={handleItalic} title="Í∏∞Ïö∏ÏûÑ">
              <span className={cx("toolbar_icon", "italic")}>I</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} onClick={handleUnderline} title="Î∞ëÏ§Ñ">
              <span className={cx("toolbar_icon", "underline")}>U</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} onClick={handleStrikethrough} title="Ï∑®ÏÜåÏÑ†">
              <span className={cx("toolbar_icon", "strikethrough")}>T</span>
            </button>

            <span className={cx("toolbar_divider")}></span>

            <div className={cx("toolbar_btn_group")}>
              <button type="button" className={cx("toolbar_btn")} onClick={() => handleAlign("Left")} title="ÏôºÏ™Ω Ï†ïÎ†¨">
                <span className={cx("toolbar_icon", "align-left")}>‚â°</span>
              </button>
              <button
                type="button"
                className={cx("toolbar_btn")}
                onClick={() => handleAlign("Center")}
                title="Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨"
              >
                <span className={cx("toolbar_icon", "align-center")}>‚â°</span>
              </button>
              <button
                type="button"
                className={cx("toolbar_btn")}
                onClick={() => handleAlign("Right")}
                title="Ïò§Î•∏Ï™Ω Ï†ïÎ†¨"
              >
                <span className={cx("toolbar_icon", "align-right")}>‚â°</span>
              </button>
            </div>

            <span className={cx("toolbar_divider")}></span>

            <button type="button" className={cx("toolbar_btn")} title="Ïù∏Ïö©Íµ¨">
              <span className={cx("toolbar_icon", "quote")}>‚ùû</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} title="ÎßÅÌÅ¨">
              <span className={cx("toolbar_icon", "link")}>üîó</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} title="Ïù¥ÎØ∏ÏßÄ">
              <span className={cx("toolbar_icon", "image")}>üì∑</span>
            </button>

            <span className={cx("toolbar_divider")}></span>

            <button type="button" className={cx("toolbar_btn")} title="Î™©Î°ù">
              <span className={cx("toolbar_icon", "list")}>‚Ä¢</span>
              <span className={cx("dropdown_arrow")}>‚ñæ</span>
            </button>
            <button type="button" className={cx("toolbar_btn")} title="Íµ¨Î∂ÑÏÑ†">
              <span className={cx("toolbar_icon", "horizontal-rule")}>‚Äî</span>
            </button>
          </div>

          <div className={cx("editor_header_right")}>
            <button className={cx("header_action_btn")}>
              Í∏∞Î≥∏ Î™®Îìú
              <span className={cx("dropdown_arrow")}>‚ñæ</span>
            </button>
          </div>
        </div>

        <div className={cx("editor_container")}>
          <div className={cx("editor_header")}>
            <div className={cx("category_select_container")}>
              <select
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                className={cx("category_select")}
                disabled={isSubmitting}
              >
                {categoryList.map((cat) => (
                  <option key={cat} value={cat}>
                    {cat}
                  </option>
                ))}
              </select>
              <div className={cx("select_arrow")}></div>
            </div>

            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              className={cx("title_input")}
              placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              disabled={isSubmitting}
            />
          </div>

          <div className={cx("editor_body")}>
            <div
              ref={editorRef}
              className={cx("content_editor", { uploading: isUploading })}
              contentEditable
              onDragOver={handleDragOver}
              onDragLeave={handleDragLeave}
              onDrop={handleDrop}
              onPaste={handlePaste}
              placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
              suppressContentEditableWarning={true}
            />
            {isUploading && <div className={cx("upload_overlay")}>Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë...</div>}
          </div>

          <div className={cx("editor_footer")}>
            <div className={cx("tag_input_container")}>
              <input
                type="text"
                value={tags}
                onChange={(e) => setTags(e.target.value)}
                className={cx("tag_input")}
                placeholder="#ÌÉúÍ∑∏ÏûÖÎ†•"
                disabled={isSubmitting}
              />
            </div>

            <div className={cx("editor_tools")}>
              <div className={cx("tool_buttons")}>
                <button className={cx("tool_btn")}>
                  <span className={cx("tool_icon")}>üìÑ</span> ÎØ∏Î¶¨Î≥¥Í∏∞
                </button>
                <button className={cx("tool_btn")}>
                  <span className={cx("tool_icon")}>üîç</span> ÎßûÏ∂§Î≤ï Í≤ÄÏÇ¨
                </button>
                <button className={cx("tool_btn")}>
                  <span className={cx("tool_icon")}>üìé</span> Í∏ÄÍº¥Ïó¥Í∏∞
                </button>
              </div>

              <div className={cx("action_buttons")}>
                <button className={cx("draft_btn")} disabled={isSubmitting}>
                  ÏûÑÏãúÏ†ÄÏû•
                </button>
                <button className={cx("publish_btn")} onClick={handleSubmit} disabled={isSubmitting || isUploading}>
                  {isSubmitting ? "Ï†ÄÏû• Ï§ë..." : "ÏôÑÎ£å"}
                </button>
              </div>
            </div>
          </div>

          {error && <div className={cx("error_toast")}>{error}</div>}
        </div>
      </div>
    </>
  );
}

export async function getStaticProps() {
  const categoryList = await getPostCategories();

  return {
    props: {
      categoryList,
    },
  };
}
